table.marker-deets {
  border: 2px solid #A40808;
  background-color: #EEE7DB;
  width: 100%;
  max-width: 600px;
  margin: auto;
  text-align: center;
  border-collapse: collapse;
}
table.marker-deets td, table.marker-deets th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}
table.marker-deets tbody td {
  font-size: 13px;
}
table.marker-deets tr:nth-child(even) {
  background: #F5C8BF;
}
table.marker-deets thead {
  background: #A40808;
}
table.marker-deets thead th {
  font-size: 19px;
  font-weight: bold;
  color: #FFFFFF;
  text-align: center;
  border-left: 2px solid #A40808;
}
table.marker-deets thead th:first-child {
  border-left: none;
}

table.marker-deets tfoot {
  font-size: 13px;
  font-weight: bold;
  color: #FFFFFF;
  background: #A40808;
}
table.marker-deets tfoot td {
  font-size: 13px;
}
table.marker-deets tfoot .links {
  text-align: right;
}
table.marker-deets tfoot .links a{
  display: inline-block;
  background: #FFFFFF;
  color: #A40808;
  padding: 2px 8px;
  border-radius: 5px;
}